export const start = async () => {
  // 
  const apps = getList()

  if (!apps.length) {
    throw new Error('')
  }

  // 
  const app = currentApp()
  console.log(' ', app)

  if (app) {
    const { pathname, hash } = window.location
    const url = pathname + hash

    window.history.pushState(url, app.name, url || app.activeRule)
    // 
    window.__CURRENT_SUB_APP__ = app.activeRule
  }

  // 
  prefetch()
}
